import React, { Component } from 'react'
import { ChevronLeft, FileText } from 'react-feather'

import AddNote from './AddNote'

export default class NotesTray extends Component {
  constructor() {
    super()
    this.state = {
      showModal: false,
      notes: [
        {
          id: 1,
          title: 'How to make money online',
          body: `For many people, making money online would be an absolute dream come true. If they could find a way to make money with a website or some other online venture, they could quit their job to focus on entrepreneurship, spend more time with their family, and finally take back control of their time and their lives.
          The crazy thing is, earning money online isn’t a pipe dream. I have been doing it for nearly a decade now with my website Good Financial Cents. I also know thousands of other people who are earning money online their own way with websites, courses, or unique marketing strategies.
          Now, here’s the good news. The majority of online revenue strategies aren’t that complicated. Like any business venture, your online income takes time to grow. You need to be willing to devote the time and energy required to get your idea off the ground, and you need grit to stick with it even if your journey is slow when you first start out.`
        },
        {
          id: 2,
          title: 'How to fall in love',
          body: `Sometimes we meet someone and we just know: it's love. However, it isn't always easy to see the wonderful qualities of the people right there in front of us. Therefore, it is good to know about some of the strategies that can make it more likely that someone will fall in love with you. Just remember that there is no way to make someone fall in love with you. All you can do is try to create the right conditions and see what happens.`
        }
      ],
      selectedNote: []
    }
  }

  modalHandler = () => {
    this.setState((prevState) => ({
      showModal: !prevState.showModal
    }))
  }

  displayNoteHandler = (noteId) => {
    this.setState({
      selectedNote: this.state.notes.filter((note) => note.id === noteId)
    })
  }

  addNoteHandler = (title, body) => {
    const newNote = {
      id: 3,
      title,
      body
    }

    this.setState({ notes: [...this.state.notes, newNote] })
  }
  render() {
    const { showNotesTray, sidedrawerHandler, notesTrayHandler } = this.props
    const { showModal, notes } = this.state
    const user = JSON.parse(localStorage.getItem('user'))
    return (
      <div
        className={showNotesTray ? 'tray text-center shadow show' : 'tray text-center shadow hide'}
        style={{ width: '25%', minWidth: '300px' }}>
        <span
          className="menu-btn mt-4 ml-3"
          title="Close"
          onClick={() => {
            notesTrayHandler()
            sidedrawerHandler()
          }}>
          <ChevronLeft size={'24'} />
        </span>
        <h5 className="mt-4">Notes</h5>
        {notes.length ? (
          <div className="w-100 text-right mt-4">
            <small
              className="badge-primary p-1 rounded-sm mr-3"
              style={{ cursor: 'pointer' }}
              onClick={this.modalHandler}>
              Add new note
            </small>
            <ul className="list-group text-left mt-4 p-2" style={{ overflowY: 'scroll', height: '90%' }}>
              {notes.map((note, i) => (
                <li
                  key={i}
                  className="list-group-item border-0 text-break d-flex align-items-center justify-content-start"
                  onClick={() => this.displayNoteHandler(note.id)}>
                  <FileText className="text-primary" size={'17'} />
                  <span className="ml-2" style={{ width: '90%' }}>
                    {note.title}
                  </span>
                </li>
              ))}
            </ul>
          </div>
        ) : (
          <div
            className="w-100 d-flex align-items-center justify-content-center"
            style={{ overflowY: 'scroll', height: '90%' }}>
            <div className="d-flex flex-column align-items-center justify-content-center">
              <span>No notes yet</span>
              <small
                className="badge-primary p-1 mt-2 rounded-sm"
                style={{ cursor: 'pointer' }}
                onClick={this.modalHandler}>
                Add note
              </small>
            </div>
          </div>
        )}
        {showModal && <AddNote modalHandler={this.modalHandler} />}
      </div>
    )
  }
}
